import {
	getBalance,
	getBill,
	checkTaskList
} from '../../api/api.js';

export default {
	data() {
		return {
			assetInfo: '',
			listInfo: '',
			labelList: 'budget',
			// budgetText: 'black',
			// taskText: 'gray',
			menuTabs: [{
				name: '收支記錄'
			}, {
				name: '任務獲取'
			}],
			scrollLeft: 0,
			currentTab: 0,
			isShow: true,
			loadingType: 10
		}
	},
	
	methods: {
		getBalanceData() {
			getBalance(0).then(data => {
				this.assetInfo = data;
			});
		},
		getBillData() {
			getBill("0").then(data => {
				this.listInfo = '';
				console.log(this.listInfo)
				this.listInfo = data.splice(0, 10);
				console.log(this.listInfo);
			});
		},
		getTaskData() {
			checkTaskList().then(data => {
				this.listInfo = '';
				console.log(this.listInfo)
				this.listInfo = data;
				console.log(this.listInfo);
			});
		},
		swichMenu: async function(current) {
			if (this.currentTab == current) {
				return false;
			}
			if (current == 1) {
				this.currentTab = current;
				this.labelList = 'task';
				this.listInfo = '';
				this.getTaskData();
				console.log(current)
			} else {
				this.currentTab = current;
				this.labelList = 'budget';
				this.listInfo = '';
				this.getBillData();
				console.log(current)
			}
		},
		// changeList(chooseItem) {
		// 	if(chooseItem == 'budget') {
		// 		this.labelList = 'budget';
		// 		this.budgetText = 'black';
		// 		this.taskText = 'gray';
		// 		this.getBillData();
		// 	} else {
		// 		this.labelList = 'task';
		// 		this.budgetText = 'gray';
		// 		this.taskText = 'black';
		// 		this.getTaskData();
		// 	}
		// }
		loadMore() {
			getBill(this.loadingType).then(data => {
				if(data == null){
					this.isShow = false
					return
				}
				this.listInfo = this.listInfo.concat(data)
				this.loadingType += 10
				console.log(this.listInfo)
			});
		}
	},
	onLoad() {
		this.getBalanceData();
		this.getBillData();
	}
}
